.images {
  display: flex;
  flex-direction: row-reverse;
}

.image {
  margin-right: -20px;
}
.image:first-child {
  margin-right: 0;
}
.imgPrew {
  position: relative;
  display: inline-block;
  padding: 2px;
  width: 64px;
  height: 64px;
  border-radius: 100px;
  border: double 2px solid;
  background-image: linear-gradient(#131316, #131316),
    linear-gradient(to top, rgba(128, 26, 178, 1), rgba(76, 76, 255, 1));
  overflow: hidden;
  background-origin: border-box;
  background-clip: content-box, border-box;
}

.img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.imgOverlay:before {
  content: "";
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background: #1c1c21;
  opacity: 0.6;
  text-align: center;
}
.counter {
  position: absolute;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
}
